body {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, sans-serif;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 40px 30px;
  padding-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  width: 200px;
  height: 231px;
}

.person__photo {
  position: relative;
}

.person__photoimg {
  border-radius: 50%;
  width: 200px;
}

.person__details {
  text-align: center;
  width: 100%;
  height: 31px;
}

.person__name {
  color: #2f4f4f;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__extra {
  display: none;
}
